import React, {Component} from 'react';
import PROJECTS from './data/projects';

class Project extends Component {
    render() {


        const {title, image, description, link, github, ghtext, linksplit, link2, link2text} = this.props.project;


        return (
            <div style = {{display: 'inline-block', width: 400, margin: 10}}>
                <hr/>
                <h3>{title}</h3>
                <hr/>
                <img src = {image} alt='profile' style={{width: 250, height: 150}}/>
                <p>{description}</p>
                <a href={link}>{link}</a>
                <br></br>
                <a href={github}><p1>{ghtext}</p1></a>
                <p1>{linksplit}</p1>
                <a href={link2}><p1>{link2text}</p1></a>
            </div>
        )
    }
}

class Projects extends Component {
    render() {
        return (
            <div>
                <h2>Highlighted Projects</h2>
                <div>
                    {
                        PROJECTS.map(PROJECT => {
                            return (
                                <Project key={PROJECT.id} project={PROJECT} />
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}

export default Projects;